import React, { useContext } from "react";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
import { useFormik } from 'formik';
import * as Yup from 'yup';

// core components
import GridItem from "components/Grid/GridItem.js";
import GridContainer from "components/Grid/GridContainer.js";
import CustomInput from "components/CustomInput/CustomInput.js";
import Button from "components/CustomButtons/Button.js";
import Card from "components/Card/Card.js";
import CardHeader from "components/Card/CardHeader.js";
import CardBody from "components/Card/CardBody.js";
import CardFooter from "components/Card/CardFooter.js";
import Box from '@mui/material/Box'



import { HOST } from '../config.js'
import { LoginContext } from "index.js";
import { useHistory } from "react-router";

const styles = {
  cardCategoryWhite: {
    color: "rgba(255,255,255,.62)",
    margin: "0",
    fontSize: "14px",
    marginTop: "0",
    marginBottom: "0",
  },
  cardTitleWhite: {
    color: "#FFFFFF",
    marginTop: "0px",
    minHeight: "auto",
    fontWeight: "300",
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
    marginBottom: "3px",
    textDecoration: "none",
  },
};

const useStyles = makeStyles(styles);

export default function UserProfile() {
    const history = useHistory();
    const {user, setUser} = useContext(LoginContext);
  const classes = useStyles();
  console.log('context',user)

  const sendRequest = (value) => {
    console.log(value)
    fetch(`${HOST}/api/login`, {
      method: 'POST',
      body: JSON.stringify(value),
      headers:{
         'Content-Type':'application/json'
      }
    }).then(res => res.json())
      .then(res => {
        console.log(res)
        if(res.token){
            console.log('word')
           setUser(res.token);
           localStorage.setItem('user', res.token);
           history.push('/admin')  
        }
        handleReset();
      })
  }
  const validationSchema = Yup.object().shape({
    password: Yup.string().min(2, 'Too Short!').max(50, 'Too Long!').required('Required'),
    email: Yup.string().email('Invalid email').required('Email is Required'),
  });

  const initValue = {
    email: '',
    password: ''
  }

  const { handleChange, handleSubmit, handleBlur, handleReset, values, touched, errors } = useFormik({
    initialValues: initValue,
    validationSchema:validationSchema,
    onSubmit: values => {
      const value = values;
      console.log(value);
      sendRequest(value);
    }
  });

  console.log(values)
  return (
    <div style={{margin: 'auto'}}>
      <Box xs={{
          margin: 'auto',
        }}>
      <GridContainer>
      
        <GridItem xs={12} sm={12} md={6} >
          <Card>
            <CardHeader color="primary">
              <h4 className={classes.cardTitleWhite}>Login</h4>
              <p className={classes.cardCategoryWhite}>Enter Credential for Login</p>
            </CardHeader>
            <CardBody>


              <GridContainer>
                <GridItem xs={12} sm={12} md={6}>
                  <CustomInput
                    labelText="Email"
                    id="name"
                    helperText={touched.email && errors.email}
                    error={Boolean(errors.email && touched.email)}
                    inputProps={{
                      name: 'email',
                      onChange: handleChange,
                      onBlur: handleBlur,
                      value: values.email
                    }}

                    formControlProps={{
                      fullWidth: true,
                    }}
                  />
                </GridItem>
                </GridContainer>
              <GridContainer>
                <GridItem xs={12} sm={12} md={6}>
                  <CustomInput
                    labelText="Password"
                    id="password"
                    helperText={touched.password && errors.password}
                    error={Boolean(errors.password && touched.password)}
                    inputProps={{
                      name: "password",
                      onChange: handleChange,
                      onBlur: handleBlur,
                      value: values.password
                    }}
                    formControlProps={{
                      fullWidth: true,
                    }}
                  />
                </GridItem>

              </GridContainer>
            </CardBody>
            <CardFooter>
              <Button onClick={handleSubmit} color="primary">Login</Button>
            </CardFooter>
          </Card>
        </GridItem>
      </GridContainer>
      </Box>
    </div>
  );
}
